/**
 *
 * @description Will return ant-design tag component base on status number.
 * @param status
 * @example setStatusTag(1)
 * @author Alon Gvili
 */
export function setStatusIcon(status: JobStatus, size: number = 16) {
  switch (status) {
    case 0: // Queue
      return (
        <FieldTimeOutlined
          size={size}
          style={{
            color: presetPrimaryColors["gray"],
          }}
        />
      );
    case 1: // Running
      return (
        <LoadingOutlined
          spin
          size={size}
          style={{ color: presetPrimaryColors["blue"] }}
        />
      );
    case 2: // Finish
      return (
        <CheckCircleOutlined
          size={size}
          style={{
            color: presetPrimaryColors["lime"],
          }}
        />
      );
    case 3: // Faild
      return (
        <CloseCircleOutlined
          size={size}
          style={{
            color: presetPrimaryColors["red"],
          }}
        />
      );
    case 4: // Waiting for feedback
      return (
        <ClockCircleOutlined
          size={size}
          style={{
            color: presetPrimaryColors["yellow"],
          }}
        />
      );
    case 5: // canceled
      return (
        <ExclamationCircleOutlined
          size={size}
          style={{
            color: presetPrimaryColors["gray"],
          }}
        />
      );
    case 6: // canceling
      return (
        <ExclamationCircleOutlined
          size={size}
          style={{
            color: presetPrimaryColors["gray"],
          }}
        />
      );
    case 9: // timedout
      return (
        <ClockCircleOutlined
          size={size}
          style={{
            color: presetPrimaryColors["red"],
          }}
        />
      );
  }
}

