import { GitSettings } from "../../../types";
import { CustomSchema } from "./types";

export const gitSettingsSchema: CustomSchema<GitSettings>[] = [
    {
        name: "remote",
        label: "Remote",
        component: "input",
        tooltip: 'Git remote URL',
        required: true,
        uniqe: true,
        rules: [{
            required: true,
            message: "Git remote can't be empty."
        }]
    },
    {
        name: "branch",
        label: "Branch",
        component: "input",
        tooltip: "Git branch. master will be used if not specified."
    },
    {
        name: "userName",
        label: "User Name",
        component: "input",
        tooltip: "User name used to access the git remote"
    },
    {
        name: "password",
        label: "Password",
        component: "password",
        tooltip: "Password or access token used to access the git remote"
    },
    {
        name: "syncBehavior",
        label: "Sync Behavior",
        component: "select",
        tooltip: "Specifies how PowerShell Universal will interact with git.",
        options: [
            { title: 'Two-Way', value: 0 },
            { title: 'One-Way', value: 1 },
            { title: 'Push Only', value: 2 },
        ]
    },
    {
        name: "initBehavior",
        label: "Initialize Behavior",
        component: "select",
        tooltip: "Specifies how PowerShell Universal will initialize the git repository. If not specified, PSU will attempt to determine the best method.",
        options: [
            { title: 'Initialize', value: 0 },
            { title: 'Clone', value: 1 },
        ]
    },
    {
        name: "interval",
        label: "Sync Interval",
        component: "inputNumber",
        tooltip: "How often to run a git sync. Defaults to every minute."
    },
];
